.header
{
  background-color: #303030;
  color: #f0f0f0;

  width: 100%;
  max-height: 16vh;
  min-height: 16vh;

  display: flex;
  align-items: center;
  flex-direction: column;

  font-size: 6vmin;

  box-shadow: 0 0 0.5vmin 0.3vmin grey;
}

.appTitle
{
  font-weight: bold;
  font-family: "Comic Sans MS", cursive, sans-serif;

  display: flex;
  align-items: center;

  margin-top: auto;
  margin-bottom: auto;
}

.appTitleLabel
{
  padding-left: 1.25vw;
}

.appLogo
{
  width: 3.2vw;
  height: 12vh;
}
.footer
{
  background-color: #303030;
  color: #F0F0F0;

  width: 100%;
  position: relative;
  
  display: flex;
  flex-direction: column;
  
  font-size: 3vmin;

  box-shadow: 0 0 0.5vmin 0.3vmin grey;
}

.appVersion
{
  font-weight: bold;
  
  margin-top: 2vh;
  margin-bottom: 2vh;
  margin-left: auto;
  margin-right: 1.25vw;
}
.navBar
{
  border-color: #303030;

  display: flex;
  align-items: center;
  flex-direction: column;
}

.navOn, .navOff
{
  background-color: white;
  cursor: pointer;
  
  border-style: solid;
  border-width: 0.1vmin;
  border-top: 0;
  border-bottom: 0;
  
  font-size: 4vmin;

  margin-left: 0.3vw;
  margin-right: 0.3vw;

  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  padding-left: 0.3vw;
  padding-right: 0.3vw;
}

.navOn
{
  color: #2BB673;
  font-weight: bold;
  text-decoration: underline;
  border-width: 0.3vmin;
}
.personFormAttributeLabel
{
  width: 4.5vw;
  padding: 0;
}
.skillFormAttributeLabel
{
  width: 6.2vw;
  padding: 0;
}
.itemEditArea
{
  min-width: 40vw;

  margin-bottom: 4vh;
  
  padding-top: 2vh;
  padding-bottom: 4vh;
  padding-left: 1vw;
  padding-right: 1vw;


  box-shadow: 0 0 0.5vmin 0.2vmin grey;
}
.searchMarginLeft
{
  margin-left: 0.5vw;
}

.searchTextField
{
  max-width: 10vw;
}
.listAreaActions
{
  width: 100%;

  display: flex;
  align-items: flex-end;
  flex-direction: column;

  margin-right: 2vw;
}
.listViewEmpty
{
  margin-bottom: 4vh;
}
.personListItemMainData
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 2vh;
}

.personListItemSkillsData
{
  display: flex;
  flex-direction: column;

  min-width: 30vh;

  margin-top: 2vh;
  margin-bottom: 4vh;
}

.personListItemSkillsLabel
{
  color: #2BB673;
  font-weight: bold;
}
.skillsListItemData
{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 2vh;
  margin-bottom: 2vh;
}


.itemEdit
{
  display: flex;
  flex-direction: column;
}

.itemEditGroupArea
{
  display: flex;
  flex-direction: column;
}

.itemEditGroupTitle
{
  color: #2BB673;
  font-weight: bold;
}

.itemEditGroup
{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: baseline;

  border: solid;
  border-color: lightgray;
  border-width: thin;

  padding-top: 0.8vh;
  padding-bottom: 0.8vh;
  padding-right: 0.45vw;
  padding-left: 0.45vw;

  margin-bottom: 3vh;
}

.itemEditGroup button
{
  display: flex;
  flex-direction: row;
  cursor: pointer;

  margin-top: 0.8vh;
  margin-bottom: 0.8vh;
  margin-left: 0.45vw;
  margin-right: 0.45vw;
}

.itemEditButtonSign
{
  background: #2BB673;

  border: solid;
  border-color: #2BB673;
  border-width: thin;
  border-radius: 100%;

  margin-left: 0.3vw;
  padding: 0;

  width: 2vmin;
  height: 2vmin;

  line-height: 2vmin;
  text-align: center;
}

.personSkillEditItemContent
{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.personSkillItemEditData
{
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 2vh;
  margin-bottom: 4vh;
}
.mainArea
{
  color: #303030;

  min-height: 66.6vh;
  position: relative;

  display: flex;
  align-items: center;
  flex-direction: column;

  margin-top: 4vh;
  margin-bottom: 6vh;
  margin-left: 0.6vw;
  margin-right: 0.6vw;

  font-size: 2.5vmin;
}

.emptySection
{
  margin-top: 1vh;
  margin-bottom: 1vh;
}
html
{
  height: 100%;
}

body
{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

html, body
{
  margin: 0px;
  padding: 0px;
}

.itTalentButton, .itTalentDarkButton, .itTalentComboBox
{
  color: white;
  font-size: 2vmin;

  border-style: solid;

  padding-top: 0.2vh;
  padding-bottom: 0.2vh;
  padding-left: 0.3vw;
  padding-right: 0.3vw;
}

.itTalentButton, .itTalentComboBox
{
  background-color: #2BB673;
  border-color: #2BB673;
}

.itTalentDarkButton
{
  background-color: #303030;
  border-color: #303030;
}

.itTalentComboBox
{
  -moz-text-align-last:center;
       text-align-last:center;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.itTalentTextField
{
  font-size: 2vmin;
  color: #303030;
  text-align: center;

  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-width: 0.3vmin;
  border-bottom-color: #2BB673;
}

.listItem
{
  display: flex;
  flex-direction: column;

  max-width: 60vw;
  
  margin-top: 2vh;
  margin-bottom: 2vh;

  padding-top: 1vh;
  padding-bottom: 1.5vh;
  padding-left: 1vw;
  padding-right: 1vw;

  box-shadow: 0 0 0.5vmin 0.2vmin grey;
}

.listItemTitle
{
  color: #2BB673;
  width: 100%;
  min-width: 40vw;
  margin-bottom: 1vh;
  text-align: center;
  font-size: 3.5vmin;
  font-weight: bold;
}

.listItemData
{
  width: 100%;
  margin-right: 0.3vw;
}

.itTalentActions
{
  display: flex;
  flex-direction: row;
}

.itTalentActions *
{
  margin-top: auto;
  margin-bottom: 0;
}

.itTalentActions button:not(:last-child) 
{
  margin-right: 0.5vw;
}

.itTalentActions button:first-child
{
  margin-left: auto;
}

.itTalentContentArea
{
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-top: 4vh;
  margin-bottom: 6vh;
}

.itTalentForm
{
  min-width: 30vw;

  margin-bottom: 4vh;
  
  padding-top: 4vh;
  padding-bottom: 4vh;

  display: flex;
  flex-direction: column;
  align-items: center;

  box-shadow: 0 0 0.5vmin 0.2vmin grey;
}

.itTalentFormAttribute
{
  margin-top: 1vh;
  margin-bottom: 1vh;

  display: flex;
  flex-direction: row;
}

.itTalentFormMessage
{
  color: #2BB673;
  font-weight: bold;

  margin-top: 4vh;
  margin-bottom: 4vh;
}

.sectionTitle
{
  color: #2BB673;
  font-weight: bold;
  font-size: 5vmin;
  margin-bottom: 4vh;
}


